// Import Vue
import Vue from 'vue'

// Import F7
import Framework7 from '../sub_modules/framework7-vue-0.9.4/sub_modules/framework7-1.6.5'

// Import F7 Vue Plugin
import Framework7Vue from '../sub_modules/framework7-vue-0.9.4'

// Import Components
import {Sticky,MyPage,BusPlugin,StorePlugin} from './index'


// Import F7 iOS Theme Styles
import Framework7Theme from '../sub_modules/framework7-vue-0.9.4/sub_modules/framework7-1.6.5/dist/css/framework7.ios.min.css'
import Framework7ThemeColors from '../sub_modules/framework7-vue-0.9.4/sub_modules/framework7-1.6.5/dist/css/framework7.ios.colors.min.css'
/* OR for Material Theme:
import Framework7Theme from 'framework7/dist/css/framework7.material.min.css'
import Framework7ThemeColors from 'framework7/dist/css/framework7.material.colors.min.css'
*/

import FontAwesome from 'font-awesome/less/font-awesome.less'

// Import App Custom Styles
import AppStyles from './css/app.less'

// Import Routes
import Routes from './routes.js'

// Import App Component
import App from './pages/test.vue'
// import App from './app.vue'


// Init F7 Vue Plugin
Vue.use(Framework7Vue)
Vue.use(Sticky)
Vue.use(MyPage)
Vue.use(BusPlugin)
Vue.use(StorePlugin)

// Init App
new Vue({
  el: '#app',
  template: '<app/>',
  // Init Framework7 by passing parameters here
  framework7: {
    root: '#app',
    /* Uncomment to enable Material theme: */
    // material: true,
    // routes: Routes,
  },
  // Register App Component
  components: {
      app: App,
      sticky: Sticky
  }
});



