<template>
    <div class="views">
        <my-ad-layer overlay class="myAdLayer">
            <div class="" style="background-color: white;width: 100%;height: 20%;text-align: center; display: table;">
                <div style="display: table-cell;vertical-align: middle;">
                    我的足迹（1/5）
                </div>
            </div>
            <div class="" style="background-color: white;width: 100%;height: 60%;text-align: center; display: table;">
                <div style="display: table-cell;vertical-align: middle;">
                    <my-swiper style="width:100%; height:200px;" effect="coverflow" :autoHeight="true">
                        <f7-swiper-slide v-for="image in images" class="bg-white color-black" style="width:65%; height:100%;">
                            <img :src="imgPath+'static/images/'+image" style="width:100%; height:100%;" />
                        </f7-swiper-slide>
                        <f7-swiper-slide v-for="image in images" class="bg-white color-black" style="width:65%; height:100%;">
                            <img :src="imgPath+'static/images/'+image" style="width:100%; height:100%;" />
                        </f7-swiper-slide>
                    </my-swiper>
                </div>
            </div>
            <div class="" style="background-color: white;width: 100%;height: 20%;text-align: center; display: table;">
                <div style="display: table-cell;vertical-align: middle;">
                    <img :src="imgPath+'static/images/10.png'" style="width:100%; height:100%;" />
                </div>
            </div>
        </my-ad-layer>
        <div class="view view-main">
            <div class="pages">
                <my-page id="app" ref="stickyContainer" class="navbar-fixed toolbar-fixed" pull-to-refresh @ptr:refresh="onRefresh" infiniteScroll @infinite="onInfinite" style="">
                    <f7-navbar back-link=" "  sliding >
                        <f7-block style="width: 100%; text-align: center; margin:0px;">
                            详情
                        </f7-block>
                    </f7-navbar>

                    <f7-swiper style="width:100%; height:200px;">
                        <f7-swiper-slide v-for="image in images" class="bg-white color-black" style="width:100%; height:200px;">
                            <img :src="imgPath+'static/images/'+image" style="width:100%; height:100%;" />
                        </f7-swiper-slide>
                    </f7-swiper>

                    <sticky :offset="40" :offset-diff="44" :check-sticky-support="true" class="myNav">
                        <div>
                            <f7-buttons>
                                <my-button tab-link="#tab1" href="#tab1" class="myTab" active>产品</my-button>
                                <my-button tab-link="#tab2" href="#tab2" class="myTab">评价</my-button>
                                <my-button tab-link="#tab3" href="#tab3" class="myTab">详情</my-button>
                                <my-button tab-link="#tab4" href="#tab4" class="myTab">推荐</my-button>
                            </f7-buttons>
                        </div>
                    </sticky>

                    <my-page-content :offset="80" id="tab1">
                        <div class="myDivider"><f7-icon fa="gift" size="16px"></f7-icon>产品</div>
                        <img :src="imgPath+'static/images/3.png'" style="width:100%; " />
                    </my-page-content>

                    <my-page-content :offset="80" id="tab2" >
                        <div class="myDivider"><f7-icon fa="commenting-o" size="16px"></f7-icon>评价</div>
                        <img :src="imgPath+'static/images/9.jpg'" style="width:100%; " />
                    </my-page-content>

                    <my-page-content :offset="80" id="tab3" tab >
                        <div class="myDivider"><f7-icon fa="picture-o" size="16px"></f7-icon>详情</div>
                        <img :src="imgPath+'static/images/7.jpg'" style="width:100%; " />
                        <img :src="imgPath+'static/images/8.jpg'" style="width:100%; " />
                    </my-page-content>

                    <my-page-content :offset="80" id="tab4" tab >
                        <div class="myDivider"><f7-icon fa="shopping-basket" size="16px"></f7-icon>推荐</div>
                        <img :src="imgPath+'static/images/5.png'" style="width:100%; " />
                        <img :src="imgPath+'static/images/6.png'" style="width:100%; " />
                    </my-page-content>

                </my-page>
            </div>
        </div>
    </div>

</template>
<script>
    import Vue from 'vue'
    // Import Components
    import {Sticky,MyButton,MyPage,MyPageContent,MyAdLayer,MySwiper,BusPlugin,StickySupportPlugin} from '../index'

    Vue.use(BusPlugin)
    Vue.use(StickySupportPlugin)

    export default {
        data: function () {
            return {
                stickySupport: Vue.$vux.stickySupport,
//                imgPath: "http://47.128.149.97:8000/",
                imgPath: "",
                images: ['1.png', '2.png']
            }
        },
        created () {
            Vue.$vux && Vue.$vux.bus && Vue.$vux.bus.$on('vux:init-myPage-completed', this.startInitSticky)
        },
        beforeDestroy () {
            Vue.$vux && Vue.$vux.bus && Vue.$vux.bus.$off('vux:init-page-completed', this.startInitSticky)
        },
        methods: {
            startInitSticky: function (msg) {
                Vue.$vux && Vue.$vux.bus && Vue.$vux.bus.$emit('vux:start-init-sticky', msg.pageContentEl.elm)
            },
            onChange: function (event){
                console.log('change');
            },
            onRefresh: function (event, done) {
                var self = this;
                console.log("navbar-fixed toolbar-fixed refresh")
                self.$f7.pickerModal('.myAdLayer')
                done();
            },
            onInfinite: function (event, done) {
                var self = this;
                console.log("navbar-fixed toolbar-fixed onInfinite")
            }
        },
        // Register App Component
        components: {
            sticky: Sticky,
            "my-button": MyButton,
            "my-page": MyPage,
            "my-page-content": MyPageContent,
            "my-ad-layer": MyAdLayer,
            "my-swiper": MySwiper
        }
    }
</script>



