<template>
  <div :class="classesObject" @tab:show="onTabShow" @tab:hide="onTabHide"><slot></slot></div>
</template>
<script>
  import Vue from 'vue'

  export default {
    data: function () {
        return {
            pageContentEl: null
        }
    },
    props: {
      'tab': Boolean,
      'active': Boolean,
      'id': String,
      'offset': Number
    },
    mounted () {
        if(Vue.$vux && Vue.$vux.bus){
            Vue.$vux.bus.$on('vux:init-myPage-completed', this.initScrollListener)
            Vue.$vux.bus.$on('vux:myPageContent-show', this.showContent)
        }
    },
    beforeDestroy () {
        if(Vue.$vux && Vue.$vux.bus){
            Vue.$vux.bus.$off('vux:init-myPage-completed', this.initScrollListener)
            Vue.$vux.bus.$off('vux:myPageContent-show', this.showContent)
        }
    },
    computed: {
      classesObject: function () {
        var self = this;
        return {
          'tab': self.tab,
          'active': self.active
        }
      }
    },
    methods: {
      onTabShow: function (e) {
        this.$emit('tab:show', e);
      },
      onTabHide: function (e) {
        this.$emit('tab:hide', e);
      },
      initScrollListener: function (msg) {
          var self = this
          self.pageContentEl = msg.pageContentEl
          var scrollBox = msg.pageContentEl.elm;
          var $el = self.$el;
          this.$nextTick(() => {
              scrollBox.addEventListener('scroll', function (e) {
                  var scrollTop = scrollBox.scrollTop + self.offset
                  var offsetTop = $el.offsetTop
                  var offsetHeight = $el.offsetHeight
                  if(offsetTop < scrollTop && scrollTop < (offsetTop + offsetHeight)){
                      Vue.$vux.bus.$emit('vux:myButton-status-change', self);
                  }
              })
          })
      },
      showContent: function (msg) {
          var self = this
          var myButton = msg
          if(myButton.tabLink == "#"+self.id){
              var offsetTop = self.$el.offsetTop - self.offset
              var scrollBox = self.pageContentEl.elm;
              scrollBox.scrollTop = offsetTop
          }
      }
    }
  }
</script>



