<script>
  import Vue from 'vue'

  import LinkMixin from '../../../sub_modules/framework7-vue-0.9.4/src/mixins/link.vue';
  export default {
    props: {
        offset: Number
    },
    render: function (c) {
      var iconEl, textEl, self = this;
      if (self.text) {
        textEl = c('span', {}, self.text);
      }
      if (self.icon || self.iconMaterial || self.iconIon || self.iconFa || self.iconF7  || self.iconIfMaterial || self.iconIfIos) {
        iconEl = c('f7-icon', {props: {
          material: self.iconMaterial,
          ion: self.iconIon,
          fa: self.iconFa,
          f7: self.iconF7,
          icon: self.icon,
          ifMaterial: self.iconIfMaterial,
          ifIos: self.iconIfIos,
          size: self.iconSize
        }})
      }
      self.classesObject.button = true;
      var linkEl = c('a', {
        class: self.classesObject,
        attrs: self.attrsObject,
        on: {
          click: self.onClick
        }
      }, [iconEl, textEl, self.$slots.default]);

      return linkEl;
    },
    mixins: [LinkMixin],
    created () {
        if(Vue.$vux && Vue.$vux.bus){
            Vue.$vux.bus.$on('vux:myButton-status-change', this.startInitListener)
            Vue.$vux.bus.$on('vux:myButton-click', this.btnClickListener)
        }
    },
    beforeDestroy () {
        if(Vue.$vux && Vue.$vux.bus){
            Vue.$vux.bus.$off('vux:myButton-status-change', this.startInitListener)
            Vue.$vux.bus.$off('vux:myButton-click', this.btnClickListener)
        }
    },
    methods: {
      startInitListener: function (msg) {
          var self = this
          var classList = self.$el.classList
          if("#"+msg.id === self.tabLink){
              if(!classList.contains("active")) classList.add("active")
          }else{
              if(classList.contains("active")) classList.remove("active")
          }
      },
      btnClickListener: function (msg) {
        var self = this
        var classList = self.$el.classList
        if(msg.tabLink === self.tabLink){
            if(!classList.contains("active")) classList.add("active")
        }else{
            if(classList.contains("active")) classList.remove("active")
        }
      },
      onClick: function (event) {
          var self = this
//          Vue.$vux.bus.$emit('vux:sticky-stickied')
          Vue.$vux.bus.$emit('vux:myButton-click', self)
          Vue.$vux.bus.$emit('vux:myPageContent-show', self)
          this.$emit('click', event)
      }
    }
  }
</script>



