<template>
  <div class="vux-sticky-box">
    <slot></slot>
  </div>
</template>

<script>
import Vue from 'vue'
import sticky from './sticky'

export default {
  name: 'sticky',
  data () {
    return {
      initTimes: 0,
      isInit: false,
      scrollBox: null
    }
  },
  created () {
      this.initListener();
  },
  activated () {
//    if (this.initTimes > 0) {
//      this.bindSticky()
//    }
//    this.initTimes++
  },
  mounted () {
      this.initListener();
  },
  beforeDestroy () {
      if(Vue.$vux && Vue.$vux.bus){
          Vue.$vux.bus.$off('vux:start-init-sticky', this.bindSticky)
          Vue.$vux.bus.$off('vux:sticky-stickied', this.stickied)
      }
  },
  methods: {
      initListener () {
          if(Vue.$vux && Vue.$vux.bus){
              Vue.$vux.bus.$on('vux:start-init-sticky', this.bindSticky)
              Vue.$vux.bus.$on('vux:sticky-stickied', this.stickied)
          }
      },
      bindSticky (msg) {
        var scrollBox = msg;
        this.scrollBox = scrollBox
        if(!this.isInit && scrollBox){
            this.$nextTick(() => {
                sticky(this.$el, {
                    scrollBox: scrollBox,
                    offset: this.offset,
                    offsetDiff: this.offsetDiff,
                    checkStickySupport: typeof this.checkStickySupport === 'undefined' ? true : this.checkStickySupport
                })
            })
            this.isInit = true;
        }
    },
    stickied () {
        var scrollBox = this.scrollBox
        var mySticky = this.$el
        if(!mySticky.classList.contains('vux-fixed')){
            scrollBox.scrollTop = mySticky.offsetTop - this.offset + 5
        }
    }
  },
  props: ['offset', 'offsetDiff', 'checkStickySupport']
}



</script>

<style lang="less">

/**
* sticky
*/
@sticky-zindex: 500;

.vux-sticky-box {
  z-index: @sticky-zindex;
}
.vux-sticky {
  width: 100%;
  position: sticky;
  top: 0;
}
.vux-fixed {
  width: 100%;
  position: fixed;
  top: 0;
  transform: translate3d(0, 0, 0);
}
.vux-sticky-fill {
  display: none;
}
.vux-fixed + .vux-sticky-fill {
  display: block;
}
</style>



